import React, { Component } from 'react';
import axios from 'axios';
import Movies from './Movies';
import './Section.scss';

class Section extends Component {
    // state : movies라는 키를 갖고 있는 객체임. kim은 사용자 정의
    state = {
        kim : []
    };
    getMovies = async () => {
        const {data:{data:{movies}}} = await axios.get("https://yts.mx/api/v2/list_movies.json"); // 여기서 movies는 배열을 갖고있음
        this.setState({ // setState() 실행 후에 다시 render() 실행
            kim : movies
        })
    }
    componentDidMount() {   // render 함수실행하고 난후에 실행됨
        this.getMovies()
    }
    render() {
        const { kim } = this.state; // 여기 kim은 객체 state가 원래 갖고 있던 키. pdf 44p.
        return (
            <section className="container">
                {
                    kim.map((data) => ( // key props 제외하고 나머지 props는 사용자정의
                    <Movies key={data.id} year={data.year} title={data.title} summary={data.summary} poster={data.medium_cover_image} genres={data.genres} />  // data뒤 id, year 등은 api 데이터내 이름. 마음대로 설정X
                    ))
                }
            </section>
        );
    }
}
// render() -> componentDidMount() -> getMovies() -> setState() -> render()
// 일반 : render() -> componentDidMount() -> setState() -> render()
export default Section;